<template>
  <div class="d-flex flex-column px-0" app style="margin-top:env(safe-area-inset-top);">
    <v-app-bar fixed class="top" app color="white" style="margin-top:env(safe-area-inset-top);">
      <v-btn fab text small @click="$router.back(-1)" class="mt-2">
        <v-icon large>mdi-arrow-left</v-icon>
      </v-btn>
      <span class="text_middle mt-2">系统消息</span>
    </v-app-bar>
    <div class="container_systemMessage" v-touch:swiperight="rightSlide">
      <v-menu offset-y>
        <template v-slot:activator="{ on, attrs }">
          <span
            class="d-flex justify-end"
            style="margin-left:50%;margin-right:10px"
            v-bind="attrs"
            v-on="on"
          >
            <span class="mr-2 text_thick">{{tabs[tab]}}</span>
            <v-icon>mdi-filter-variant</v-icon>
          </span>
        </template>
        <v-list>
          <v-list-item v-for="(item, index) in tabs" :key="index" @click="tab=index">
            <v-list-item-title :class="tab==index?'red--text':''">{{ item }}</v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>
      <div :class="tab==0?'':'d-none'">
        <div v-if="deleteMessage.length==0" class="d-flex justify-center">
          <v-icon color="primary">mdi-emoticon-cool-outline</v-icon>
          <span class="ml-1">暂无消息</span>
        </div>
        <v-list v-else>
          <div v-for="(item,i) in deleteMessage" :key="i">
            <v-list-item>
              <v-list-item-avatar>
                <v-icon color="primary">mdi-message-text</v-icon>
              </v-list-item-avatar>
              <v-list-item-content>
                <v-list-item-title>系统消息 {{item.type}}处理#{{item.id}}</v-list-item-title>
                <div class="d-flex flex-column">
                  <span class="line_one grey--text text_small--">您发表的"{{item.content}}"</span>
                  <span
                    class="grey--text text_small--"
                  >因为违反社区规范【五.{{item.reason}}】：发表“{{processType[item.reason]}}”被删除</span>
                </div>
              </v-list-item-content>
              <v-list-item-action>
                <v-list-item-action-text>{{item.add_time.slice(5,10)}}</v-list-item-action-text>
              </v-list-item-action>
            </v-list-item>
            <v-divider></v-divider>
          </div>
        </v-list>
      </div>
      <div :class="tab==1?'':'d-none'">
        <div v-if="processMessage.length==0" class="d-flex justify-center">
          <v-icon color="primary">mdi-emoticon-cool-outline</v-icon>
          <span class="ml-1">暂无消息</span>
        </div>
        <v-list>
          <div v-for="(item,i) in processMessage " :key="i">
            <v-list-item>
              <v-list-item-avatar>
                <v-icon color="primary">mdi-message-text</v-icon>
              </v-list-item-avatar>
              <v-list-item-content>
                <v-list-item-title>系统消息 处理#{{item.id}}</v-list-item-title>
                <div class="d-flex flex-column">
                  <span
                    class="grey--text text_small--"
                  >因为累计删帖删评论,您将被禁言到{{item.end_time.slice(0,16).replace('T',' ')}}</span>
                </div>
              </v-list-item-content>
              <v-list-item-action>
                <v-list-item-action-text>{{item.add_time.slice(5,10)}}</v-list-item-action-text>
              </v-list-item-action>
            </v-list-item>
            <v-divider></v-divider>
          </div>
        </v-list>
      </div>
      <!-- <v-divider class="mt-1"></v-divider> -->
    </div>
  </div>
</template>

<script>
const processType = {
  1: "违反《中华人民共和国宪法》等(一)中规定的法律法规的内容",
  2: "侮辱或诽谤他人或特定群体的内容",
  3: "侵犯他人隐私的内容",
  4: "侵犯他人知识产权、商业机密的内容",
  5: "散布谣言和恐慌，可能会误导他人的内容",
  6: "含有任何淫秽、色情、性暗示、不当暴露的内容",
  7: "鼓励、教唆他人实施违法犯罪、暴力、非法集会的内容",
  8: "含有隐喻、容易引起歧义的缩写、代名词、特定表述等对于政治等敏感问题进行引导、讨论",
  9: "含有隐喻、容易引起歧义的缩写、代名词、特定表述等对于政治等敏感问题进行引导、讨论",
  10: "含有自杀/自残倾向性的内容",
  11: "干扰社区正常运营，大量重复内容垃圾发帖等",
  12: '引起他人不适的内容。"引起他人不适" 定义为经过社区用户大量举报的内容',
  13: "经本人要求删除涉及个人隐私、过多细节或对本人生活造成不良影响的本人发帖"
};
import touch from "../../utils/touch";
export default {
  name: "systemMessage",
  data() {
    return {
      processType: {
        1: "违反《中华人民共和国宪法》等(一)中规定的法律法规的内容",
        2: "侮辱或诽谤他人或特定群体的内容",
        3: "侵犯他人隐私的内容",
        4: "侵犯他人知识产权、商业机密的内容",
        5: "散布谣言和恐慌，可能会误导他人的内容",
        6: "含有任何淫秽、色情、性暗示、不当暴露的内容",
        7: "鼓励、教唆他人实施违法犯罪、暴力、非法集会的内容",
        8: "含有隐喻、容易引起歧义的缩写、代名词、特定表述等对于政治等敏感问题进行引导、讨论",
        9: "含有隐喻、容易引起歧义的缩写、代名词、特定表述等对于政治等敏感问题进行引导、讨论",
        10: "含有自杀/自残倾向性的内容",
        11: "干扰社区正常运营，大量重复内容垃圾发帖等",
        12: "引起他人不适的内容",
        13: "经本人要求删除涉及个人隐私、过多细节或对本人生活造成不良影响的本人发帖"
      },
      deleteMessage: [],
      processMessage: [],
      tab: 0,
      tabs: ["删除消息", "禁言消息"]
    };
  },
  created() {
    if (sessionStorage.getItem("SystemMessageNumTemp"))
      localStorage.setItem(
        "SystemMessageNum",
        sessionStorage.getItem("SystemMessageNumTemp")
      );
    let that = this;
    that
      .$request("get", "/user/getSystemMessage", {}, {})
      .then(res => {
        res = res.data;
        (that.processMessage = res.processMessage),
          (that.deleteMessage = res.deleteMessage);
      })
      .catch(e => {
        that.showSnackbar(e);
      });
  },
  methods: {
    rightSlide() {
      this.$router.go(-1);
    }
  },
  directives: {
    touch
  }
};
</script>

<style>
.container_systemMessage {
  margin-top: 60px;
}
</style>


